/*! Overlay Button */
.btn_overlay {
  top: 0;
  left: 0;
  width: auto;
  z-index: 99;
  width: 28px;
  position: absolute;
  visibility: hidden;
  opacity: 0;
  filter: alpha(opacity=0);
  transition: all 0.3s ease-in;
  -moz-transition: all 0.3s ease-in;
  -o-transition: all 0.3s ease-in;
  -webkit-transition: all 0.3s ease-in;
  -ms-transition: all 0.3s ease-in;
  background: transparent none repeat scroll 0 0; }
  .btn_overlay .btnhide {
    float: left;
    width: 28px;
    color: #ededed;
    border: 0 none;
    margin: 0px 0px;
    min-height: 28px;
    max-height: 28px;
    line-height: 28px;
    position: relative;
    -webkit-border-radius: 0px;
    border-radius: 0px;
    border: 1px solid rgba(0, 0, 0, 0.9);
    transition: all 0.3s ease-in;
    -moz-transition: all 0.3s ease-in;
    -o-transition: all 0.3s ease-in;
    -webkit-transition: all 0.3s ease-in;
    -ms-transition: all 0.3s ease-in;
    background: none repeat scroll 0 0 rgba(0, 0, 0, 0.9); }
    .btn_overlay .btnhide:not(:first-child) {
      border-top: 0px none !important; }
    .btn_overlay .btnhide:hover, .btn_overlay .btnhide:active {
      border: 1px solid rgba(0, 0, 0, 0.1);
      background: none repeat scroll 0 0 rgba(70, 165, 252, 0.9); }
    .btn_overlay .btnhide .fa-thumb-tack {
      -webkit-transform: rotate(-33deg);
      /* Chrome, Safari, Opera */
      -moz-transform: rotate(-33deg);
      /* Mozilla Firefox */
      -o-transform: rotate(-33deg);
      -ms-transform: rotate(-33deg);
      /* IE 9 */
      transform: rotate(-33deg); }
      .btn_overlay .btnhide .fa-thumb-tack::after {
        left: 5%;
        right: 5%;
        z-index: 0;
        height: 6%;
        bottom: -5%;
        content: "";
        position: absolute;
        -webkit-border-radius: 70%;
        border-radius: 70%;
        -webkit-transform: scaleY(0.1);
        /* Chrome, Safari, Opera */
        -moz-transform: scaleY(0.1);
        /* Mozilla Firefox */
        -o-transform: scaleY(0.1);
        -ms-transform: scaleY(0.1);
        /* IE 9 */
        transform: scaleY(0.1);
        -webkit-box-shadow: 0 0 6px 6px rgba(0, 0, 0, 0.1), 0 0 15px 10px rgba(0, 0, 0, 0.1), inset 0 0 10px 20px rgba(0, 0, 0, 0.25);
        -moz-box-shadow: 0 0 6px 6px rgba(0, 0, 0, 0.1), 0 0 15px 10px rgba(0, 0, 0, 0.1), inset 0 0 10px 20px rgba(0, 0, 0, 0.25);
        -ms-box-shadow: 0 0 6px 6px rgba(0, 0, 0, 0.1), 0 0 15px 10px rgba(0, 0, 0, 0.1), inset 0 0 10px 20px rgba(0, 0, 0, 0.25);
        box-shadow: 0 0 6px 6px rgba(0, 0, 0, 0.1), 0 0 15px 10px rgba(0, 0, 0, 0.1), inset 0 0 10px 20px rgba(0, 0, 0, 0.25); }

